import styled from "styled-components";

const EmptyListContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: 1200px) {
    margin-top: 4rem;
  }
`;

const SVGContainer = styled.svg`
  align-self: center;
  justify-content: center;
  height: 200px;
  width: 242px;
`;

const NothingHere = styled.h1`
  margin-top: 4rem;
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: -0.625px;

  color: ${({ theme }) => theme.text};
`;

const Description = styled.p`
  width: 242px;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  /* or 125% */

  text-align: center;
  letter-spacing: -0.25px;

  color: ${({ theme }) => theme.greyText};

  .newInvoice {
    font-weight: 700;
  }
`;
